@import url(https://fonts.googleapis.com/css?family=Lato:300,300i,400,400i,700,700i,900,900i|Roboto+Condensed:400,400i,700,700i&display=swap);

* {
    margin: 0;
    padding: 0;
}
 
img {
    max-width: 100%;
    height: auto;
}
 
h1,h2,h3,h4,h5,h6{
    margin: 0 0 15px;
    font-weight: 700;
    font-family: 'Roboto Condensed', sans-serif;
}
 
a:hover, a:focus{
    text-decoration: none;
    outline: none;
    border: none;
}
 
body{
  font-family: 'Lato', sans-serif;
  background-color: #f2f2f2;
}

.loader {
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}




.post-thumbnail {
    height: 450px;
    width: 100%;
    background-color: #ddd;
    position: relative;
}

.post-thumbnail img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.single-post-item {
    background-color: #fff;
    padding: 20px;
    margin-top: 30px;
    border-radius: 4px;
}

.post-meta span {
    margin-right: 8px;
    color: #777;
    text-transform: capitalize;
}

.post-meta span i.fa {
    padding-right: 5px;
    color: #444;
}

.post-meta {
    margin: 10px 0 12px;
}

a.boxed-btn {
    background-color: #13c39a;
    color: #fff;
    padding: 10px 25px;
    margin-top: 20px;
    display: inline-block;
}

ol, ul {
    margin-left: 15px;
}
.header-top {
    padding: 25px 0;
}

.logo h3 {
    margin: 0;
    text-transform: uppercase;
    font-size: 25px;
    letter-spacing: 1px;
    font-size: 45px;
    text-align: center;
    font-weight: 300;
}

.logo h3 a {
    color: #000;
}

.mainmenu ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.mainmenu ul li {
    display: inline-block;
}

.mainmenu ul li a {
    display: block;
    text-transform: uppercase;
    padding: 16px 30px;
    color: #fff;
}

.mainmenu {
    background-color: #13c39a;
    border-bottom: 3px solid #039c78;
}

.mainmenu ul li:first-child a i.fa {
    padding-right: 5px;
}

.mainmenu ul li a.active {
    background-color: #039c78;
}
